@import "global";

.Button {
  width: $dim;
  height: $dim;
  padding: 0;
  text-indent: -9999px;
  background-size: $dim;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  &.ADD {
    background-image: url("%PUBLIC_URL%/images/add_64x64.png"); }
  &.EDIT {
    background-image: url("%PUBLIC_URL%/images/submit_64x64.png"); }
  &.DELETE {
    background-image: url("%PUBLIC_URL%/images/remove_64x64.png"); } }
