@import "colors";
@import "global";

.Item {

  $paddingLeft: $spacing * 4;

  .ItemBody {
    height: $height;
    margin-bottom: $bottomSpacing;
    background-color: $lightGray;
    transition: background-color .2s;
    cursor: pointer;
    &:hover {
      background-color: lighten($lightGray, 2%); }

    > span {
      display: inline-block;
      width: calc(100% - #{ $dim * 2 + $spacing * 2 + $paddingLeft });
      text-align: left;
      padding-left: $paddingLeft; }

    button {
      visibility: hidden;
      margin-top: $spacing;
      margin-right: $spacing; }
    &:hover {
      button {
        visibility: visible; } } }

  form {
    margin-bottom: $bottomSpacing;
    input {
      width: calc(100% - #{ $dim + $spacing });
      margin-right: $spacing;
      height: $height;
      box-sizing: border-box;
      border: solid 1px $lightGray;
      padding-left: $paddingLeft; } } }
